body {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  background-color: #DAE0E6;
}

div {
  display: block;
}

p {
  font-family: Noto Sans,Arial,sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  word-wrap: break-word;
}

a {
  color: #1a1a1b;
}

a, .bold-text a,
.profile-dropdown a {
  text-decoration: none;
}

a, button {
  font-family: Noto Sans Bold,Arial,sans-serif;
}

textarea {
  resize: none;
}

.bold-text {
  font-weight: bold;
  font-size: 12px;
  line-height: 20px;
  color: #1c1c1c;
  font-family: IBM Plex Sans Bold,Arial,sans-serif;
}

.light-text {
  font-weight: 100;
  font-size: small;
  font-family: IBM Plex Sans,Arial,sans-serif;
  opacity: 0.7;
}

.medium-text {
  font-family: IBM Plex Sans Medium,Arial,sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  color: #1a1a1b;
}



.main-cont {
  box-sizing: border-box;
  display: flex;
  max-width: 100%;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  margin-top: 10px;
}

.post-links {
  background: #fff;
  border: none;
  color: gray;

  ---fa-counter-background-color: black;
}

.post-links i {
}


.post-cont {
  min-height: 1000px;
  width: 100%;
  margin-top: 28px;
}



.single-post {
  border: 1px solid #fff;
  margin-bottom: 10px;
  overflow: hidden;
  position: relative;
  /* cursor: pointer; */
  background-color: #fff;

  display: flex;
  flex-direction: row;
  border-radius: 4px;
}

.single-post-footer {
  /* display: flex; */
  /* flex-direction: column; */
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  padding: 5px;
  color: grey;
  max-width: fit-content;
}


.single-post-footer:hover {
  /* background: grey;
  opacity: .5; */
}

.single-post-view {
  border: 1px solid #fff;
  margin-bottom: 10px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  background-color: #fff;
}

.single-post-btn-bar {
  display: flex;
  flex-direction: column;

  justify-content: space-between;

}
.vote-cont {
  display: flex;
  flex-direction: column;

}

#upVoteButton:hover .icon_svg-stroke{

  stroke: #ff4500;
}


#downVoteButton:hover .icon_svg-stroke{

  stroke: #0079D3;
}

#said-it-btn:hover{
  color: #ff4500;
}

.sound-cont {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  align-content: flex-end;
}

.single-post:hover {
  /* border: 1px solid gray; */
  /* box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, .5); */
  /* z-index: 1; */
}

.text-post-content {
  max-height: 250px;
  word-break: break-word;
  background: #fff;
  display: block;
  -webkit-mask-image: linear-gradient(180deg, #000 60%, transparent);
  mask-image: linear-gradient(180deg, #000 60%, transparent);
  padding-right: 10px;
}

.text-post-content p {
