    position: absolute;
    height: 80px;
    width: 200px;    
    right: 0;
    background: linear-gradient(to right, rgba(255,255,255,.6) 30%,rgba(255,255,255,1) 100%);
    background: -webkit-gradient(linear, left top, right top, color-stop(0%,rgba(255,255,255,.6)), color-stop(100%,rgba(255,255,255,1)));
    background: -webkit-linear-gradient(left, rgba(255,255,255,.6) 30%,rgba(255,255,255,1) 100%);
    top: 0; */
}

.text-post-view {
  line-height: 21px;
  padding-bottom: 1px;
  margin-bottom: -1px;
  padding-left: 15px;
  overflow: auto;
}


.side-page-cont {
  margin-left: 24px;
  margin-top: 28px;
  flex: 0 0 312px;
  width: 312px;
  border-radius: 4px;
}


.side-page {
  width: inherit;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.side-page-post-view {
  width: inherit;
  display: flex;
  flex-direction: column;
  max-height: 50%;
  min-height: 50%;
}

.side-header {
  border: 1px solid #FFF;
  background-color: #fff;
  border-radius: 4px;
  overflow: visible;
  word-wrap: break-word;
  /* border: 1px solid black; */
  padding: 0;
}

.side-header ol {
  padding-top: 50px;
}

.post-cont-topbar {
  align-items: center;
  border: 1px solid #fff;
  border-radius: 4px;
  box-sizing: border-box;

  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin-bottom: 16px;
  padding: 10px 12px;

  background-color: #fff;
}

.top-com-list {
  height: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 12px;
  border-bottom: thin solid #CCC;
}

.top-com-list img {
  padding-right: 5px;
}


.personal-home-cont {
  padding: 12px;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  background-color: #fff;
}

.p-home-banner {
  background-image: url('home-banner.png');
  background-size: cover;

  width: 100%;
  background-position-y: center;
  background-position-x: center; 
  background-repeat: no-repeat;
  border-radius: 4px;
  height: 34px;
}

.p-home-header {
  display: flex;
  align-items: center;
  justify-content: left;
  width: 100%;
}

.p-home-header > p {
  margin-left: 10px;
  margin-top: 30px;
}

#p-home-moogle {
  background: url('snoo-home.png');
  background-size: 40px 68px;
  height: 68px;
  width: 40px;


}


#create-links button, a {
  justify-content: center;
  margin-top: 12px;
  width: 100%;
}

.main-spacer {
  margin-top: 16px;
  width: 312px;
  background-color: #fff;
}

#banner {
  height: 64px;
  text-align: center;
  /* background-color: #ff4500; */
  background: #2B303A;
  /* background: linear-gradient(45deg, #2B303A, rgb(95, 95, 95), #2B303A); */
  max-width: 312px;
  width: 100%;
  position: absolute;
  border-radius: 4px;
}

#banner > h2 {
  padding: 5px;
  /* padding-left: 100px; */
  display: flex;
  justify-content: center;
  /* position: absolute; */
}


.community-logo {
  border-radius: 50%;
}


@media screen and (min-width: 640px) {
  .main-cont {
    padding: 20px 24px;
  }
}


@media screen and (min-width: 960px) {
  .post-cont {
    width: 640px;
  }
}


@media screen and (min-width: 640px) {
  .single-post-content {
    border-radius: 4px;
    padding-left: 40px;
    width: 100%;
  }
}
